.mobile-body {
    width: 100%;
    min-height: 1080px;
    background-color: #1E1E1E;
    border-right: 1px solid #FFFFFF;
}

.mobile-body-header {
    border-bottom: 1px solid #FFFFFF;
}

.mobile-body-header-language {
    position: relative;
}

.english-lang {
    position: absolute;
    left: 0;
    bottom: 0;
}

.vietnamese-lang {
    position: absolute;
    right: 0;
    bottom: 0;
}

.mobile-menu-game-item {
    height: 150px;
    width: 100%;
    position: relative;
    border-top: 1px solid #FFFFFF;
    border-bottom: 1px solid #FFFFFF;
    border-right: 1px solid #FFFFFF;
    cursor: pointer;
}

.mobile-menu-game-item-icon {
    position: absolute;
    left: 8.67%;
    right: 62.89%;
    top: 16.17%;
    bottom: 58.98%;
    border-radius: 25px;
    width: 80px;
    height: 80px;
}

.mobile-menu-game-item-name {
    position: absolute;
    left: 44.44%;
    right: 0%;
    top: 20.54%;
    bottom: 61.76%;

    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 25px;

    color: #FFFFFF;
}

.mobile-menu-game-item-rating {
    position: absolute;
    left: 44.44%;
    right: 11.11%;
    top: 60.24%;
    bottom: 58.98%;
    width: 100%;
}

.mobile-menu-game-item-review-counter {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 10px;
    text-align: center;
    color: #FFFFFF;
}
