.game-content {
    flex: 1;
    width: 100%;
}

.game-content-background {
    marginTop: 1px;
    width: 100%;
    height: 100%;
    background-color: #1E1E1E;
    position: relative;
}

.game-content-description {
    margin-top: -200px;
    padding: 20px;
    position: relative;
}

.game-content-download {
    position: absolute;
    right: 5%;
}

.game-content-game-name {
    font-family: Roboto;
    font-style: normal;
    font-weight: bold;
    font-size: 30px;
    /* or 145% */

    color: #FFFFFF;
    padding-bottom: 12px;

    display: flex;
}

.game-content-game-description {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 22px;
    line-height: 30px;
    /* or 136% */

    color: #FFFFFF;
}

.game-content-game-rating {
    padding-left: 50px;
}

.game-content-game-rating-counter {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #FFFFFF;
}
