.mobile-game-content-header {
    position: relative;
    width: 100%;
    height: 50px;
}

.mobile-game-content-header-close {
    position: absolute;
    right: 1px;
}

.mobile-game-content-name {
    width: 100%;
    height: 30px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 25px;
    text-align: center;

    color: #FFFFFF;
}

.mobile-game-content-rating {
    width: 100%;
    height: 40px;
    text-align: center;
}

.mobile-game-content-rating-counter {
    width: 100%;

    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    line-height: 25px;
    text-align: center;

    color: #FFFFFF;
}

.mobile-game-content-download {
    width: 100%;
    text-align: center;
}

.mobile-game-content-description {
    width: 100%;

    padding-top: 10px;

    font-family: Roboto;
    font-style: normal;
    font-weight: 300;
    font-size: 15px;
    line-height: 25px;

    color: #FFFFFF;
}
