.left-menu {
    width: 300px;
    min-height: 1080px;
    background-color: #1E1E1E;
    border-right: 1px solid #FFFFFF;
}

.left-menu-header {
    height: 180px;
    width: 300px;
    border-right: 1px solid #FFFFFF;
}

.left-menu-game-item {
    height: 130px;
    width: 300px;
    position: relative;
    border-top: 1px solid #FFFFFF;
    border-bottom: 1px solid #FFFFFF;
    border-right: 1px solid #FFFFFF;
    cursor: pointer;
}

.left-menu-game-item-icon {
    position: absolute;
    left: 8.67%;
    right: 62.89%;
    top: 20.17%;
    bottom: 58.98%;
    border-radius: 25px;
    width: 80px;
    height: 80px;
}

.left-menu-game-item-name {
    position: absolute;
    left: 44.44%;
    right: 0%;
    top: 25.54%;
    bottom: 61.76%;

    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    color: #FFFFFF;
}

.left-menu-game-item-rating {
    position: absolute;
    left: 44.44%;
    right: 11.11%;
    top: 65.24%;
    bottom: 58.98%;
    width: 100%;
}

.left-menu-game-item-review-counter {
    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 10px;
    text-align: center;
    color: #FFFFFF;
}
